import React, {PropTypes} from 'react';
import $ from 'jquery';
import {StyledSearchField} from '../../styledComponents/styledComponents.jsx';


export default class SearchField extends React.Component {
  constructor(props) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  onClick(e){
    e.preventDefault();
    //alert($('.podcast-search-input').val());
    const parametersList = "term=all&media=podcast&limit=3";
    fetch('https://itunes.apple.com/search?' + parametersList  ,{method : "get",
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(data => {
                     //console.log(JSON.stringify(data));
                    var results = data.results;
                     var res = results.map(result => result.country)
                     //console.log(res);
                     console.log(JSON.stringify(data));

             })
             .catch(err => {

                        alert(JSON.stringify(err));
             });
  }
  render() {
    return (<StyledSearchField>
       <input type="text" className="podcast-search-input"
         placeholder="give in a podcast show name" />
       <button type="submit" onClick={this.onClick}>start off</button>
    </StyledSearchField>);
  }
}

SearchField.propTypes ={
};



