import C from './constants';


export const hotResult = (state = {} , action) => {
  switch (action.type) {
    case C.ADD_HOT_RESULT:
      return {
        wrapperType : action.wrapperType,
        artistId : action.artistId,
        collectionId : action.collectionId,
        trackId : action.trackId,
        artistName : action.artistName,
        collectionName : action.collectionName,
        trackName : action.trackName,
        artworkUrl : action.artworkUrl,
        artistViewUrl : action.artistViewUrl,
        feedUrl : action.feedUrl,
        releaseDate : action.releaseDate,
        country : action.country,
        genres : action.genres
      }
    default:
      return state ;

  }
}

export const hotResults = (state = [] , action) => {
  switch (action.type) {
    case C.ADD_HOT_RESULT:
      return [
        ...state,
        hotResult({},action)
      ]
    case C.UPDATE_HOT_RESULTS :
      return action.newData.map(item => ({
        wrapperType : item.wrapperType,
        artistId : item.artistId,
        collectionId : item.collectionId,
        trackId : item.trackId,
        artistName : item.artistName,
        collectionName : item.collectionName,
        trackName : item.trackName,
        artworkUrl : item.artworkUrl,
        artistViewUrl : item.artistViewUrl,
        feedUrl : item.feedUrl,
        releaseDate : item.releaseDate,
        country : item.country,
        genres : item.genres
      }));
    case C.INITIALIZE_HOT_RESULTS :
       return []
    case C.REMOVE_HOT_RESULT :
      return state.filter(s => s.collectionId !== action.collectionId && s.trackId !== action.trackId)
    default:
      return state ;

  }
}

export const flattredItem = (state = {} , action) => {
  switch (action.type) {
    case C.ADD_FLATTRED:
      return {
        wrapperType : action.wrapperType,
        artistId : action.artistId,
        collectionId : action.collectionId,
        trackId : action.trackId,
        artistName : action.artistName,
        collectionName : action.collectionName,
        trackName : action.trackName,
        artworkUrl : action.artworkUrl,
        artistViewUrl : action.artistViewUrl,
        feedUrl : action.feedUrl,
        releaseDate : action.releaseDate,
        country : action.country,
        genres : action.genres
      }
    default:
      return state ;

  }
}

export const flattredItems = (state = [] , action) => {
  switch (action.type) {
    case C.ADD_FLATTRED:
      return [
        ...state,
        flattredItem({},action)
      ]
    case C.REMOVE_FLATTRED:
      return state.filter(s => s.collectionId !== action.collectionId && s.trackId !== action.trackId)
    case C.INITIALIZE_FLATTRED :
      return []
    default:
      return state ;

  }
}

export const token = (state = {} , action) => {
  switch (action.type) {
    case C.UPDATE_TOKEN:
      return {
        ...state,
        value : action.tokenValue
      }
    default :
      return state ;
  }
}

export const user = (state = {}, action) => {
  switch (action.type) {
    case C.UPDATE_USER:
      return {
        ...state,
        username : action.username,
        email : action.email,
        fullname : action.name,
        country : action.country
      }
    case C.INITIALIZE_USER :
      return {}
    default :
      return state ;
  }
}



