import React, {PropTypes} from 'react';
import {StyledPodcastItem} from '../../styledComponents/styledComponents.jsx';

export default class PodcastItem extends React.Component {
  render() {
    const {item} = this.props;
    return (
      <StyledPodcastItem>
          <div className="artWork">
            <img src={require(`${item.artworkUrl}`)} alt="" />
          </div>
          <div className="infos">
             <p>podcast infos</p>
          </div>
      </StyledPodcastItem>);
  }
}

PodcastItem.propTypes = {
};



