import styled,{css} from 'styled-components';
import {injectGlobal} from 'styled-components';

injectGlobal`
  @import url('https://fonts.googleapis.com/css?family=Josefin+Sans|Changa+One|Macondo|Exo+2|Acme|Sigmar+One');
  `



export const flexRow = styled.div`
   display : flex;
   flex-direction : row;

`

export const flexColumn = styled.div`
   display : flex;
   flex-direction : column;

`
export const StyledAppTemplate = styled.div`
  position : absolute;
  width : 100%;
  top : 0 ;
  left : 0 ;
  background: #82e1e1;
  height : 100vh;
  overflow-y: auto;

  /*#5db3e9*/
`

export const AppTemplateBody = styled.div`
  margin : 148px 0 0 0 ;
  /*border-top: 1px solid black;*/
`

export const StyledAppBar = styled.div`
  position : absolute;
  top : 0 ;
  left : 0 ;
  text-align: left;
  width : 100%;
  height : 149px;
  /*border-bottom: 1px solid black;*/
  background-image: url('../../common/images/appbarback.png');
  background-repeat: repeat-x;
  z-index : 10;
`

export const BarLeftBox = styled.div`
  float : left;
  margin : 10px 0 0 100px;
  display: flex;
  flex-direction: row;

`

export const BarRightBox = styled.div`
  float: right;
  margin : 12px 40px 0 0;
  display: flex ;
  flex-direction: row;
  /*border : 1px solid black;*/
  justify-content: space-between;

`

export const StyledLink = styled.a`
  text-decoration: none;
  color : #444;
  &:hover {
    text-decoration: none;
  }
  &:active {
    color : #ddd;
  }
  &:focus {
    text-decoration: none
  }
`

export const StyledBarIcon = styled.img`
  width : 100px;
  height : 70px ;
`


export const StyledAppMenu = styled.ul`
  list-style: none ;
  margin : 15px 0 0 40px;
  padding : 0;
`

export const AppMenuItem = styled.li`
  display: inline;
  margin : 0 0 0 30px;
  padding : 0 0 17px 0 ;
  font-family: 'Patua One', cursive;
  ${({active}) => active && css`
    border-bottom : 4px solid #2014ac;
  `  }

`

export const AppMenuLink = styled.a`
    color : #333;
    text-decoration: none;
    cursor: pointer;
    font-size: 25px;
    font-weight: 600;
    font-family: "Acme";
    padding : 5px 8px;
    border-radius: 8px;
    border : 2px solid black;

    transition: background-color .8s ease;
    &:hover {
      background-color: #9fe5fb;
      color : white;

    }

`

export const StyledSearchField = styled(flexColumn)`
   width : 700px;
   margin: auto;

   p {
     font-size: 20px;
     color : #55f;
   }

   input {
     border-radius: 20px;
     height : 50px ;
     width : 600px;
     font-size: 20px;
     margin : 0 auto 10px auto;
     padding : 5px 0 ;
     border: 3px dashed #31e25b;
     text-align : center;

     &:focus{
       outline: none;
     }

     ::placeholder{

     }
   }

   button{
     width : 80px;
     height : 30px;
     margin : auto;
     background: white;
     color : #18d447;
     font-size: 15px;
     font-weight: bold;
     border : 2px solid #18d447;
     border-radius: 10px;
     cursor: pointer;

     transition: height .5s ease;
     transition: background: .5s ease;
     &:hover{
       /*background: #cff1a4;*/
       height : 35px;
     }

     &:focus {
       outline : none
     }
   }
`

export const StyledPodcastItems = styled(flexColumn)`
   width : 1200px;
   height : 700px ;
   padding : 10px;
   margin : auto;
   border-radius: 7px;
   background: white;
   box-shadow: 1px 1px 3px #a1a0a2;
`

export const StyledPodcastItem  = styled(flexRow)`
  width : 800px;
  height : 100px;
  padding : 10px 15px;
  margin : auto ;
  background: #adeaf7;
  box-shadow: 1px 1px 3px #a1a0a2;
`

export const PodHeader = styled.div`
  height : 140px;
  background: #82e1e1;
  padding-top: 50px;
  /*border : 1px solid black;*/

`

export const PodBody = styled.div`
   margin : 40px 0 0 0;
`

export const SigninBody = styled(flexColumn)`
    /*border : 1px solid black;*/
    margin : 280px 0 0 0;

`

export const SignInButton = styled.button`
  padding : 0 10px;
  width : 250px;
  height : 60px ;
  color : black ;
  margin : auto;
  font-size: 17px;
  font-weight: 900;
  font-family: 'Josefin Sans', sans-serif;
  border : 3px solid black;
  border-radius: 10px;
  cursor : pointer;
  background-color:transparent;
  transition: color .5s ease-in-out;
  transition: background-color .5s ease-in-out;
  &:hover {
    background-color: black;
    color : white

  }

  &:focus{
    outline : none
  }
`



