import React from 'react';
import PropTypes from 'prop-types';
import AppTemplate from '../appTemplate.jsx';
import SearchField from './searchField.jsx';
import PodcastItems from './podcastItems.jsx';
import {PodHeader,PodBody} from '../../styledComponents/styledComponents.jsx';


export default class PodcastsDashboard extends React.Component {
  render() {
    const {store} = this.context;
    return (
      <AppTemplate>
            <PodHeader>
               <SearchField />
            </PodHeader>
            <PodBody>
               <PodcastItems store={store}  />
            </PodBody>
      </AppTemplate>);
  }
}

PodcastsDashboard.propTypes = {
};

PodcastsDashboard.contextTypes = {
  store : PropTypes.object
}



